.MapFilter h1 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 5px;
}

.MapFilter p {
  text-align: center;
  font-size: 18px;
  margin-top: 0;
}

.MapFilter .row {
  display: flex;
  justify-content: space-between;
  padding: 0 20px 0 20px;
}

.MapFilter .row p {
  font-size: 18px;
  padding-top: 18px;
}

.MapFilter .row .container {
  display: flex;
  flex-direction: row;
  gap: 5px;
}

.MapFilter .available input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.MapFilter .available label {
  display: flex;
  position: relative;
  width: 60px;
  height: 40px;
  margin-top: -10px;
  border-radius: 40px;
  background: #ececec;
  cursor: pointer;
}

.MapFilter .available label:after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 30px;
  height: 30px;
  background: #fff;
  border-radius: 30px;
  transition: 0.3s;
}

.MapFilter .available input:checked + label {
  background: #7dff92;
}

.MapFilter .available input:checked + label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

.MapFilter .available label:active:after {
  width: 30px;
}

.MapFilter .connector input[type="checkbox"] {
  display: none;
}

.MapFilter .connector label:before {
  content: "";
  display: flex;
  width: 50px;
  height: 50px;
  margin-top: 4px;
  border-radius: 50px;
  background: #ececec;
  background-repeat: no-repeat;
  background-position: center;
}

.MapFilter .connector.GBT label:before {
  background-image: url("./img/GBTForMapFilter.svg");
  background-size: 40px;
}

.MapFilter .connector.CCS label:before {
  background-image: url("./img/CCSComboForMapFilter.svg");
  background-size: 40px;
}

.MapFilter .connector input:checked + label:before {
  background-color: #7dff92;
}

.MapFilter .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 20px 20px 0 20px;
  padding: 10px;
  border-radius: 10px;
  background: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
}

.MapFilter .button p {
  font-size: 16px;
  margin: 0;
}
