.navigation {
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 77px;
  padding: 6px 38px 0 30px;
  background-color: white;
  box-shadow: 0 -5px 5px -5px rgba(215, 215, 215, 0.5);
  box-sizing: border-box;
  z-index: 100;
}

.navigation div {
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.navigation p {
  position: absolute;
  top: 35px;
}

.navigation .default {
  color: #a9a9a9;
}

.navigation .active {
  color: #4a79ff;
}

.navigation .map {
  width: 27px;
  height: 39px;
}

.navigation .list {
  margin-top: 2px;
  width: 32.5px;
  height: 35.5px;
}

.navigation .profile {
  margin-top: 2px;
  width: 28px;
  height: 33px;
}
