@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: "PT Sans", sans-serif;
  min-width: 100%;
  width: 100%;
  max-width: 100%;
  min-height: 100%;
  height: 100%;
  max-height: 100%;
}

h1 {
  font-size: 26px;
  text-align: center;
}

p {
  font-size: 16px;
}

.top {
  display: flex;
  flex-direction: row;
  justify-content: left;
  padding-left: 20px;
}

a {
  color: black;
  text-decoration: none;
}

.button {
  width: 40px;
  height: 40px;
  padding-right: 5px;
}

.mapAll {
  width: 100%;
  height: 100%;
}

.mapContainer {
  display: flex;
  width: 100%;
  height: 100vh;
}

.placemarkVolume {
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  font-weight: 700;
  color: #aaaaaa;
  width: 25px;
  height: 25px;
  margin-left: 5px;
  margin-top: 19px;
  text-align: center;
}

.placemarkAlva {
  color: #4a79ff;
}

.plus {
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: black;
  z-index: 500;
}

.minus {
  display: flex;
  position: absolute;
  top: 30px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: black;
  z-index: 500;
}

.location {
  display: flex;
  position: absolute;
  top: 60px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: rgb(223, 62, 62);
  z-index: 500;
}

.filterrr {
  display: flex;
  position: absolute;
  top: 90px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: rgb(4, 217, 171);
  z-index: 500;
}

.controls {
  position: absolute;
  right: 20px;
  bottom: calc(50% + -30px);
  display: flex;
  flex-direction: column;
  width: 38px;
  height: 148px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 0 5px rgba(215, 215, 215, 0.5);
  z-index: 500;
  padding-left: 5px;
  padding-top: 5px;
  opacity: 1;
}

.controls-none {
  display: none;
}

@keyframes unvisibility {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.controls img {
  padding-top: 2px;
  padding-bottom: 2px;
  width: 32px;
  height: 32px;
}
