.passwordChange header div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 45px 0 10px;
}

.passwordChange p {
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  line-height: 25px;
}

.passwordChange .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 10px 10px 0 10px;
  padding: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  background: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
}

.passwordChange .button p {
  margin: 0;
}

.passwordChange form {
  display: flex;
  flex-direction: column;
  height: 40px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 0px;
}

.passwordChange form label {
  display: flex;
  flex-direction: column;
}
.passwordChange .default {
  margin: 0px 10px 10px 10px;
  padding: 10px;
  border: solid 2px rgb(199, 199, 199);
  border-radius: 10px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

.passwordChange .requestSum {
  color: #c0c0c0;
}

.passwordChange .mistake {
  color: rgb(255, 111, 111);
}
