.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 20px;
  max-width: 100%;
  padding-right: 10px;
  padding-bottom: 10px;
}

.stationForMapRow {
  display: flex;
  flex-direction: row;
  padding-left: 10px;
  background-color: #fdfdfd;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 15px;
  border-radius: 20px;
  box-shadow: 0 0 10px 3px #ffffff;
}

.stationForMapRowActive {
  display: flex;
  flex-direction: row;
  padding-left: 10px;
  background-color: #fdfdfd;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 10px;
  border-radius: 20px;
  box-shadow: 0 0 10px 3px #4eec8d;
}

.stationForMapRow p {
  font-size: 16px;
}

.stationForMapRowInner {
  display: flex;
  width: 75%;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 10px;
}

.stationForMapRowInnerLeft {
  display: flex;
  flex-direction: row;
}

.stationForMapImage {
  height: 40px;
  width: 40px;
  margin-left: 0;
  padding-top: 5px;
  padding-right: 2px;
}

.stationForMapImage2 {
  height: 30px;
  width: 30px;
  padding-top: 10px;
  padding-left: 5px;
  padding-right: 7px;
}

.stationForMapRowStatus {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 30%;
  border-radius: 0 20px 20px 0;
  color: white;
}

.Available {
  background-color: #60cb7c;
}

.Unavailable {
  background-color: #cb6060;
}

.Preparing {
  background-color: rgb(241, 255, 85);

  color: black;
}

.Charging {
  background-color: rgb(241, 255, 85);
  color: black;
}

.Finishing {
  background-color: rgb(241, 255, 85);

  color: black;
}

.Faulted {
  background-color: rgb(241, 255, 85);

  color: black;
}

.stationName {
  font-family: "PT Sans", sans-serif;
  font-size: 22px;
  font-weight: 700;
  padding-left: 25px;
  margin-bottom: 0;
}

.stationAddress {
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  padding-left: 25px;
  margin-top: 10px;
  padding-bottom: 10px;
  text-align: left;
}

.station .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 20px 20px 0 20px;
  padding: 10px;
  border-radius: 10px;
  background: #4a79ff;
  border-color: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
}

.station .button.default {
  background: #ffffff;
  color: #c9c9c9;
  border: solid 1px rgb(225, 225, 225);
  cursor: pointer;
}

.station .button p {
  font-size: 16px;
  margin: 0;
}
