.profile .user {
  padding-left: 20px;
}

.profile .user p {
  font-size: 22px;
}

.profile .wallet {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: auto;
  height: 200px;
  margin: 20px;
  padding: 20px;
  border-radius: 20px;
  background-color: #4a79ff;
  color: white;
  box-sizing: border-box;
}

.profile .wallet p {
  font-size: 22px;
  margin: 0;
  text-align: left;
}

.profile .wallet .wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.profile .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 10px 20px 0 20px;
  padding: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  background: #ffffff;
  color: #d9d9d9;
  cursor: pointer;
}

.profile .button p {
  margin: 0;
}

.profile .blue {
  background-color: #4a79ff;
  color: #d9d9d9;
}

.profile .privacyPolicy {
  padding-left: 20px;
  color: #d9d9d9;
}
