.sessionField {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  background-color: #fdfdfd;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 10px;
  border-radius: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.sessionField p {
  font-size: 16px;
  line-height: 28px;
  margin: 0;
  padding: 0;
}

.sessionField .description {
  color: #606060;
  font-size: 16px;
}

.sessionField .value {
  color: black;
  font-size: 18px;
  font-weight: 700;
}

.value.static {
  color: #a9a9a9;
}

.stationName {
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: 700;
  padding-left: 25px;
  margin-bottom: 0;
}

.stationAddress {
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding-left: 25px;
  margin-top: 10px;
}

.session .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 30px 20px 0 20px;
  padding: 10px;
  border-radius: 10px;
  background: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
}

.session .button p {
  font-size: 16px;
  margin: 0;
}
