.privacyPolicy header div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 10px 0 20px;
  max-width: 100%;
}

.privacyPolicy p {
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  line-height: 25px;
}

.privacyPolicy b {
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  line-height: 25px;
}

.privacyPolicy .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 10px 20px 0 20px;
  padding: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  background: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
  margin-top: 20px;
  margin-bottom: 20px;
}

.privacyPolicy .button p {
  margin: 0;
}
