.userEdit header div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 45px 0 10px;
}

.userEdit p {
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  line-height: 25px;
}

.userEdit .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 10px 10px 0 10px;
  padding: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  background: #4a79ff;
  color: #d9d9d9;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
}

.userEdit .button p {
  margin: 0;
}

.userEdit form {
  display: flex;
  flex-direction: column;
  height: 40px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 0px;
}

.userEdit form label {
  display: flex;
  flex-direction: column;
}
.userEdit .default {
  margin: 0px 10px 10px 10px;
  padding: 10px;
  border: solid 2px rgb(199, 199, 199);
  border-radius: 10px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

.userEdit .requestSum {
  color: #c0c0c0;
}
