.list header div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 10px 0 20px;
  max-width: 100%;
}

.list .search {
  display: flex;
  width: 100%;
  height: 40px;
  margin-right: 10px;
}

