}

.list .search input {
  width: 100%;
  margin: 0;
  padding: 10px 10px 10px 35px;
  border: solid 2px rgb(225, 225, 225);
  border-radius: 10px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

.list .preview,
.list .param {
  display: flex;
  justify-content: space-between;
  padding: 0 20px 0 20px;
  border-bottom: solid 1px rgb(225, 225, 225);
}
