:is(.signIn, .signUp) header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 45px 0 10px;
}

:is(.signIn, .signUp) header p {
  font-size: 24px;
  text-align: center;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.signIn header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 0 45px;
}

.signIn header p {
  padding: 10px 15px 0 0;
  color: #4a9eff;
}

:is(.signIn, .signUp) .logo {
  display: flex;
  flex-direction: row;
  margin-top: 25vh;
  width: 100vw;
  justify-content: center;
}

:is(.signIn, .signUp) .logo img {
  width: 70vw;
  margin-left: -5vw;
}

:is(.signIn, .signUp) form {
  display: flex;
  flex-direction: column;
  height: 40px;
  width: 100%;
  margin-top: 20px;
}

:is(.signIn, .signUp) form p {
  display: flex;
  justify-content: center;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

:is(.signIn, .signUp) form label {
  display: flex;
  flex-direction: column;
}

:is(.signIn, .signUp) .default {
  margin: 0 10px 10px 10px;
  padding: 10px;
  border: solid 2px rgb(199, 199, 199);
  border-radius: 10px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

:is(.signIn, .signUp) .mistake {
  margin: 0 10px 10px 10px;
  padding: 10px;
  border: solid 2px rgb(255, 195, 195);
  border-radius: 10px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
  animation-duration: 0.3s;
  animation-name: horizontal-shaking;
  animation-iteration-count: 1;
}

@keyframes horizontal-shaking {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(2px);
  }
  50% {
    transform: translateX(-2px);
  }
  75% {
    transform: translateX(2px);
  }
  100% {
    transform: translateX(0);
  }
}

:is(.signIn, .signUp) .submit {
  display: flex;
  justify-content: right;
}

:is(.signIn, .signUp) .submit input[type="image"] {
  padding-right: 10px;
}

:is(.signIn, .signUp) input[type="submit"] {
  min-height: 45px;
  font-family: "PT Sans", sans-serif;
  font-size: 16px;
}

:is(.signIn, .signUp) .badrequest {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  margin: 0;
  color: rgb(255, 111, 111);
  text-align: center;
}

:is(.signIn, .signUp) .button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: auto;
  height: 20px;
  margin: 0 10px 0 10px;
  padding: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  background: #ffffff;
  color: #d9d9d9;
  cursor: pointer;
}

:is(.signIn, .signUp) .button p {
  margin: 0;
}

:is(.signIn, .signUp) .blue {
  background-color: #4a79ff;
  color: #d9d9d9;
}
